body {
	background-color: rgb(31, 108, 172);
	padding: 20px;
	margin: 0;
}
h1,
h2,
p,
ul,
li {
	font-family: sans-serif;
}

h1 {
	margin-block-end: 0px;
}

h3 {
	margin-block-start: 0px;
}
ul.header li {
	display: inline;
	list-style-type: none;
	margin: 0;
}
ul.header {
	background-color: #111;
	padding: 0;
}
ul.header li a {
	color: #fff;
	font-weight: bold;
	text-decoration: none;
	padding: 20px;
	display: inline-block;
}
.content {
	background-color: #fff;
	padding: 20px;
}
.content h2 {
	padding: 0;
	margin: 0;
}
.content li {
	margin-bottom: 10px;
}
.active {
	background-color: #f0361ea9;
}
